import { useEffect, useState } from 'react';
import '../mobilePalan/mobilePlan.css';
import PlanList from './planList/PlanList';

function MobilePlan({ allOperatorDat, selectAmount }) {
  const [activeTab, setActiveTab] = useState(0);
  const [totalData, setTotalData] = useState({});
  const [activeKey, setActiveKey] = useState('');

  const tab1 = allOperatorDat?.plans ? Object.keys(allOperatorDat.plans) : [];
  const tab2 = allOperatorDat?.offerPlan ? Object.keys(allOperatorDat?.offerPlan) : [];
  const tabs = [...tab1, ...tab2];

  useEffect(() => {
    const Data1 = allOperatorDat?.plans || {};
    const Data2 = allOperatorDat?.offerPlan || {};
    const total = { ...Data1, ...Data2 };
    setTotalData(total);
    if (tabs.length > 0) {
        setActiveKey(tabs[0]);
      }
  }, [allOperatorDat]);




  const handleTabChange = (index, data) => {
    setActiveKey(data);
    setActiveTab(index);
  };

  return (
    <>
      {allOperatorDat && (
        <div className="tabs d-flex overflow-auto flex-column">
          <div className="tab-buttons">
            {tabs.map((tab, index) => (
              <button
                key={index}
                className={`${index === activeTab ? 'active' : ''}`}
                onClick={() => handleTabChange(index, tab)}
              >
                {tab}
              </button>
            ))}
          </div>
          <div className="tab-content">
            <PlanList data={totalData[activeKey] || []} selectAmount={selectAmount} />
          </div>
        </div>
      )}
    </>
  );
}

export default MobilePlan;